import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import './navbar.css';
import ModalSearch from '../ModalSearch/modalsearch';

const Navbar = () => {
    const [menu, setMenu] = useState(null);
    const [menuOpen, setMenuOpen] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [keyword, setKeyword] = useState('');
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        navigate(`/movie/search/keyword/${keyword}`);
        setShowModal(false); 
        setKeyword(''); // Clear the input field
    };

    const handleClick = (category) => {
        setMenu(category);
        setMenuOpen(false);
    };

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const handleShowModal = () => {
        setShowModal(true);
    };

    const handleCloseModal = () => {
        setShowModal(false);
    };

    return (
        <>
            <div className='navbar_container'>
                <div className={`navbar ${menuOpen ? 'active' : ''}`}>
                    <div className='logo'>
                        <Link style={menu === "trang_chu" ? { borderBottom: "none" } : {}} onClick={() => handleClick('trang_chu')} to={'/'}>
                            <img src={"https://movix-taupe.vercel.app/assets/movix-logo-d720c325.svg"} alt="logo" />
                        </Link>
                    </div>
                    <button className='menu-toggle' onClick={toggleMenu}>
                        {menuOpen ? '✖' : '☰'}
                    </button>
                    <div className={`bar ${menuOpen ? 'active' : ''}`}>
                        <ul>
                            <li><Link style={menu === "trang_chu" ? { borderBottom: "none" } : {}} onClick={() => handleClick('trang_chu')} to='/'>Trang Chủ</Link></li>
                            <li><Link style={menu === "phim_moi_cap_nhat" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_moi_cap_nhat')} to='/movie/phim_moi_cap_nhat'>Tất Cả Phim</Link></li>
                            <li><Link style={menu === "phim_le" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_le')} to='/movie/phim_le'>Phim Lẻ</Link></li>
                            <li><Link style={menu === "phim_bo" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_bo')} to='/movie/phim_bo'>Phim Bộ</Link></li>
                            <li><Link style={menu === "tvshow" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('tvshow')} to='/movie/tvshow'>Tv Show</Link></li>
                            <li><Link style={menu === "phim_hoat_hinh" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_hoat_hinh')} to='/movie/phim_hoat_hinh'>Phim Hoạt Hình</Link></li>
                            <li onClick={handleShowModal} style={{ color: "#fff", cursor: "pointer" }}><i className="fa-solid fa-magnifying-glass"></i></li>
                        </ul>
                    </div>
                </div>
            </div>
            <ModalSearch show={showModal} handleClose={handleCloseModal}>
                <form onSubmit={handleSubmit}>
                    <div className='modal_search'>
                        <input required value={keyword} onChange={(e) => setKeyword(e.target.value)} placeholder='Search for a Movie or TV Show...'></input>
                        <button type='submit'>Search</button>
                    </div>
                </form>
            </ModalSearch>
        </>
    );
};

export default Navbar;
